html {
  scroll-behavior: smooth;
  background-color: #070707;
  color: #DEB992;
  font-family: 'Roboto', sans-serif;
}

section {
  margin: 1%;
} 

/* 
Colour palette

Important text #1BA098
Hover color #0d2e46
Background #051622
Body text #DEB992
*/

#navbar {
  color: #1BA098;
}

.navbar-link {
  color: inherit;
  font-style: inherit;
  text-decoration: none;
}

@keyframes pop {
  0% {
    transform: translateX(0%);
    transform: translateY(0%);
  }

  50% {
    transform: translateY(4%);
  }

  100% {
    transform: translateY(0%);
  }
}

.navbar-link:hover {
  animation-name: pop;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

#About {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#about-image-div {
  height: 300px;
  width: 300px;
  overflow: hidden;
  border-radius: 50%;
}

.about-info {
  text-align: center;
}

#Contact {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.app-logo {
  height: 50px;
  margin-left: 20px;
  margin-right: 20px;
  filter: invert(53%) sepia(43%) saturate(763%) hue-rotate(127deg) brightness(89%) contrast(87%);
}

@keyframes bob {
  25% {
    transform: translateY(-5%);
  }

  50% {
    transform: translateY(0%);
  }

  75% {
    transform: translateY(2%);
  }

  100% {
    transform: translateY(0%);
  }
}

.app-logo:hover {
  animation-name: bob;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear; /* Chrome, Safari, Opera */
  animation-timing-function: linear;
  cursor: pointer;
}

#Projects, #Interests {
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#interest-items {
  width: 65%;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.interest-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 50%;
}

@keyframes appear {
  20% {
    scale: 0.9;
  }

  49% {
    visibility: visible;
  }
  
  50% {
    transform: translateY(-130%);
    scale: 1.1;
  }

  100% {
    visibility: visible;
    scale: 1;
    transform: translateY(-130%)
  }
}

.interest-quote {
  visibility: hidden;
  position: relative;
  top: -70%;
  z-index: -1;
  animation: appear;
  animation-duration: 1s;
  animation-play-state: paused;
  animation-fill-mode: forwards;
}

.interest-image {
  justify-self: center;
  align-self: center;
  max-width: 100%;
  max-width: 100%;
  z-index: 1;
}

.interest-quote.hovered {
  animation-play-state: running;
}
